import { Component, Input, OnInit } from '@angular/core';
import { QuestionBaseComponent } from '../question-base.component';
import { GameService } from 'src/services/game.service';
import { GamePoint, ShortAnswerQuestion } from 'src/models/models';

@Component({
  selector: 'app-short-answer',
  templateUrl: './short-answer.component.html',
})
export class ShortAnswerComponent extends QuestionBaseComponent implements OnInit {

  @Input()
  point: GamePoint<ShortAnswerQuestion>;

  constructor(
    gameService: GameService
  ) {
    super(gameService);
  }

  ngOnInit() { }

  isCorrect(input: string | string[]): boolean {
    input = (input as string).trim().toLowerCase();
    return this.point.body.possibleAnswers.some(i => i.trim().toLowerCase() === input);
  }

}
