  async ngOnInit() {
    // console.log(encodeURIComponent(JSON.stringify(this.game)));

    if (!this.game) {
      // await this.info.alert('Game does not exits');
      return;
    }

    this.setUpMap();

    this.setupSubscriptions();
  }

  setupSubscriptions() {

    const watchLocation$ = this.locationService.watchLocation();

    watchLocation$.pipe(first()).subscribe(() => {
      this.setUpMarkers();
      this.playerFeature = this.setUpUser();
    });

    watchLocation$
      .pipe(
        takeUntil(this.destroy$)
        // catchError((err: Error) => {
        //   console.log(err);
        //   return of(err.message);
        // })
      )
      .subscribe(loc => {
        this.updateUserLocation(loc);
      });


    this.gameService.enablePoints$
      .pipe(
        takeUntil(this.destroy$)
      )
      .subscribe(loc => {
        this.enableMarkers(loc);
      });

    this.gameService.disablePoints$
      .pipe(
        takeUntil(this.destroy$)
      )
      .subscribe(loc => {
        this.disableMarkers(loc);
      });



  }

  setUpMap() {
    const [x, y] = this.game.location;
    const zoom = this.game.zoom;
    const center = transformFromGeoLocation2([y, x]);
    // const center = [x, y];
    // console.log(center);

    this.map = new Map({
      view: new View({
        center,
        // center: [0, 0],
        zoom,
      }),
      layers: [
        new TileLayer({
          source: new OSM(),
        }),
      ],
      target: 'ol-map',
    });

    const vectorSource = new VectorSource<Point>({
      features: [],
    });

    this.markerSource = vectorSource;

    const vectorLayer = new VectorLayer({
      source: vectorSource,
    });

    this.map.addLayer(vectorLayer);

    this.map.on('moveend', (e) => {
      const newZoom = this.map.getView().getZoom();
      console.log(newZoom);
      // if (currZoom != newZoom) {
      //   console.log('zoom end, new zoom: ' + newZoom);
      //   currZoom = newZoom;
      // }
    });

    // display popup on click
    this.map.on('click', (evt) => {
      const feature: Feature<Point> = this.map.forEachFeatureAtPixel(evt.pixel, (feature1) => feature1) as any;

      // disposePopover();
      if (!feature) {

        return;
      }

      this.onFeatureClicked(evt, feature);
    });

    setTimeout(() => {
      this.map.setTarget(this.mapElementRef.nativeElement);

    });
  }

  onFeatureClicked(evt: ol.MapBrowserEvent<any>, feature: Feature<Point>) {
    console.log(feature);

    const data: MarkerData = feature.get('data');
    if (!data.clickable) { return; }

    // this.markerSource.removeFeature(feature);
    const loc = transformToGeoLocation(feature.getGeometry().getCoordinates());
    const userLoc = transformToGeoLocation(this.playerFeature.getGeometry().getCoordinates());

    const distance = this.locationService.distance(loc[1], userLoc[1], loc[0], userLoc[0]);
    const isClose = this.gameService.isCloseEnough(distance);
    console.log(distance);
    // menuController.open();

    this.gameService.pointSelected({
      point: data,
      distance,
      isClose
    });

  }

  setUpMarkers() {

    this.game.points.forEach(p => {
      if (!p.visible) { return; }
      this.showMarker(p);
    });
  }

  showMarker(p: GamePoint) {
    const [x, y] = p.location;
    const center = transformFromGeoLocation2([y, x]);
    this.addMarker(center, {
      clickable: true,
      img: p.img || 'assets/x_marker.png',
      ...p
    } as MarkerData);
  }

  getRegexExpr(names: string[]) {
    return names
      .map(i => {
        const m = i.replace(/\*/g, '.*');
        return new RegExp(m, 'g');
      });
  }

  enableMarkers(names: string[]) {
    const regs = this.getRegexExpr(names);
    this.game.points
      .filter(point => regs.some(r => point.name.match(r)))
      .forEach(point => {
        point.visible = true;
        this.showMarker(point);
      });
  }

  disableMarkers(names: string[]) {
    const regs = this.getRegexExpr(names);

    const gnames = this.game.points
      .filter(point => regs.some(r => point.name.match(r)))
      .map(i => i.name);

    for (const name of gnames) {
      const features = this.features[name];
      if (!features) { continue; }

      features.forEach(f => this.markerSource.removeFeature(f));
      delete this.features[name];
    }
  }

  setUpUser() {
    return this.addMarker([0, 0], {
      clickable: false,
      img: 'assets/current_location.png'
    } as MarkerData);
  }

  ngOnDestroy(): void {
    console.log('destroying');
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }

  addMarker(loc: Coordinate, data: MarkerData) {
    const iconFeature = new Feature({
      geometry: new Point(loc),
      data
    });

    const iconStyle = new Style({
      image: new Icon({
        anchor: [0.5, 46],
        anchorXUnits: 'fraction',
        anchorYUnits: 'pixels',
        src: data.img,
      }),
    });

    iconFeature.setStyle(iconStyle);

    this.markerSource.addFeature(iconFeature);
    this.features[data.name] = [...(this.features[data.name] || []), iconFeature];

    // const vectorSource = new VectorSource({
    //   features: [iconFeature],
    // });

    // this.markerSource = vectorSource;

    // const vectorLayer = new VectorLayer({
    //   source: vectorSource,
    // });

    // this.map.addLayer(vectorLayer);

    return iconFeature;

  }

  updateUserLocation(loc: GeolocationPosition) {
    if (!this.playerFeature) { return; }

    // console.log(loc);

    this.playerFeature.getGeometry().setCoordinates(transformFromGeoLocation(loc));
  }

