import { Component, Directive, Input } from '@angular/core';
import { GamePoint, QuestionType } from 'src/models/models';
import { GameService } from 'src/services/game.service';


// @Directive({
//   // selector: 'app-multiple-choice',
//   // templateUrl: './multiple-choice.component.html',
// })
export abstract class QuestionBaseComponent {

  // @Input()
  abstract point: GamePoint<QuestionType>;

  constructor(
    protected gameService: GameService
  ) { }

  onSubmit(value: string | string[]) {
    const result = this.isCorrect(value);
    const valueArr = Array.isArray(value) ? value : [value];
    this.gameService.answerSelected(this.point, result ? 'correct' : 'incorrect', valueArr);
  }

  abstract isCorrect(input: string | string[]): boolean;

}
