import { Component, Input, OnInit } from '@angular/core';
import { QuestionBaseComponent } from '../question-base.component';
import { GameService } from 'src/services/game.service';
import { GamePoint, MultipleChoiceQuestion } from 'src/models/models';

@Component({
  selector: 'app-multiple-choice',
  templateUrl: './multiple-choice.component.html',
})
export class MultipleChoiceComponent extends QuestionBaseComponent implements OnInit {

  @Input()
  point: GamePoint<MultipleChoiceQuestion>;

  selected: string;
  answered = false;

  constructor(
    gameService: GameService
  ) {
    super(gameService);
  }

  // get isDisabled() {
  //   return !this.selected || this.answered;
  // }

  ngOnInit() { }

  select() {

    this.onSubmit(this.selected);
  }

  isCorrect(answer: string): boolean {
    const result = this.point.body.possibleAnswers.includes(answer);

    if (result) {
      this.answered = true;
    }

    return result;
  }

}
