/* eslint-disable no-underscore-dangle */
import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil, filter } from 'rxjs/operators';
import { GamePoint, QuestionType, NoteResult } from 'src/models/models';
import { GameService } from 'src/services/game.service';
import { MenuController } from '@ionic/angular';
import { InfoService } from '../../../../services/info.service';
import { SelectedPoint } from 'src/models/models';

@Component({
  selector: 'app-game-point',
  templateUrl: './game-point.component.html',
  styleUrls: ['./game-point.component.scss'],
})
export class GamePointComponent implements OnInit, OnDestroy {

  private _selectedPoint: SelectedPoint;

  @Input()
  set selectedPoint(v: SelectedPoint) {
    this._selectedPoint = v;
    this.showFeedback = false;
    if (!v) { return; }
    this.status = !v.isClose
      ? 'not_close'
      : v.explored ? 'explored'
        : 'close';
  }

  get selectedPoint() { return this._selectedPoint; }

  destroy$ = new Subject();

  showFeedback = false;
  isCorrect = false;

  constructor(
    private gameService: GameService,
    private menuController: MenuController,
    private info: InfoService
  ) { }

  get pointAny() { return this.point as any; }

  get point() { return this._selectedPoint.point as GamePoint<QuestionType>; }

  // get isExplored() { return this._selectedPoint.explored; }

  status: 'not_close' | 'close' | 'explored' = 'not_close';

  ngOnInit() {
    // console.log(this.point);

    const evnts = this
      .gameService
      .answerSelected$
      .pipe(
        takeUntil(this.destroy$));

    evnts
      .pipe(
        filter(i => (['correct', 'incorrect'] as NoteResult[]).includes(i.result))
      ).subscribe(i => {
        // console.log(i)

        this.showFeedback = true;
        this.isCorrect = i.result === 'correct';

      });

    evnts
      .pipe(filter(i => i.result === 'ok'))
      .subscribe(i => {
        this.gameService.moveNext(this.point);
        this.menuController.close();
      });
  }

  ok() {
    this.gameService.answerSelected(this.point, 'ok');
  }

  async giveUp() {
    const result = await this.info.confirm('Give up', `Are you sure to give up?`);
    if (!result) { return; }

    this.ok();
  }

  ngOnDestroy(): void {
    console.log('destro');

    this.destroy$.next();
    this.destroy$.unsubscribe();
  }

}
