/* eslint-disable prefer-arrow/prefer-arrow-functions */
import { Coordinate } from 'ol/coordinate';
import * as proj from 'ol/proj';

// eslint-disable-next-line prefer-arrow/prefer-arrow-functions
export function transformFromGeoLocation(loc: GeolocationPosition) {
  return proj.transform([loc.coords.longitude, loc.coords.latitude], 'EPSG:4326', 'EPSG:3857');
}

// eslint-disable-next-line prefer-arrow/prefer-arrow-functions
export function transformFromGeoLocation2(loc: Coordinate): Coordinate {
  return proj.transform([loc[0], loc[1]], 'EPSG:4326', 'EPSG:3857');
}

// eslint-disable-next-line prefer-arrow/prefer-arrow-functions
export function transformToGeoLocation(loc: Coordinate): Coordinate {
  return proj.transform([loc[0], loc[1]], 'EPSG:3857', 'EPSG:4326');
}
