import { Component, OnDestroy, OnInit } from '@angular/core';
import { MenuController } from '@ionic/angular';
import { BehaviorSubject, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { Game, Player, SelectedPoint } from 'src/models/models';
import { GameService } from 'src/services/game.service';
import { InfoService } from '../../services/info.service';
;

@Component({
  selector: 'app-game-map',
  templateUrl: 'game-map.page.html',
  styleUrls: ['game-map.page.scss'],
  providers: [
    MenuController
  ]
})
export class GameMapPageComponent implements OnInit, OnDestroy {

  title$ = new BehaviorSubject('Menu');
  destroy$ = new Subject();

  // gamePoint: GamePoint<QuestionType>;
  selectedPoint: SelectedPoint;

  game: Game;
  player: Player;

  constructor(
    private menuController: MenuController,
    private gameService: GameService,
    private info: InfoService
  ) { }

  ngOnInit(): void {
    this.menuController.swipeGesture(false);

    this.gameService.pointSelected$
      .pipe(
        takeUntil(this.destroy$)
      )
      .subscribe(point => {
        if (!point) {
          return;
        }

        console.log(point);
        // this.gamePoint = point.point as any;
        this.selectedPoint = point;

        this.title$.next(point.point.title);
        this.menuController.open();
      });

    this.gameService
      .gameSelected$
      .pipe(takeUntil(this.destroy$))
      .subscribe(game => {
        if (!game) {
          return;
        }
        this.game = game;
        this.info.alert(`${game.name} loaded`);
      });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }

}
